                        </div>
                    </a>
                </div>
            </div>
        </nav>
        <div class="container is-content">
            <section class="head">
                <img class="logo" alt="Vue logo" src="./assets/logo.png">

                <h1>Vue.js slide to unlock component</h1>
                <div class="description">
                    <b>Protect users from accidental clicks for Vue 3.x</b>
                </div>

                <div class="install">
                    npm install --save vue-slide-unlock
                </div>
            </section>

            <section class="demo-section">
                <slide-unlock
                    :auto-width="getAutoWidth"
                    :width="width"
                    :height="height"
                    :text="text"
                    :success-text="successText"
                    :handler-icon="handlerIcon"
                    :success-icon="successIcon"
                    :circle="getShape"
                />
            </section>

            <section class="props-section">
                <h2>Options (props)</h2>

                <div class="grid">
                    <div class="item">
                        <label for="autoWidth">
                            <input
                                id="autoWidth"
                                v-model="autoWidth"
                                type="checkbox"
                                true-value="true"
                                false-value="false"
                            >
                            autoWidth
                        </label>
                    </div>
                    <div class="item">
                        <label for="autoWidth">
                            <input
                                id="isCircle"
                                v-model="isCircle"
                                type="checkbox"
                                true-value="true"
                                false-value="false"
                            >
                            isCircle
                        </label>
                    </div>
                    <div class="item">
                        <label>width (px)</label>
                        <input v-model="width" type="number" :disabled="autoWidth === 'true'">
                    </div>
                    <div class="item">
                        <label>height (px)</label>
                        <input v-model="height" type="number">
                    </div>
                    <div class="item">
                        <label>text</label>
                        <input v-model="text" type="text">
                    </div>
                    <div class="item">
                        <label>successText</label>
                        <input v-model="successText" type="text">
                    </div>
                    <div class="item">
                        <label>textSize</label>
                        <input v-model="textSize" type="text">
                    </div>
                </div>
            </section>
            <section class="colors-section">
                <h2>Colors (CSS Variables)</h2>

                <div class="grid">
                    <div class="item">
                        <label>textColor</label>
                        <input v-model="textColor" type="color">
                    </div>
                    <div class="item">
                        <label>textCompleteColor</label>
                        <input v-model="textCompleteColor" type="color">
                    </div>
                    <div class="item">
                        <label>background</label>
                        <input v-model="background" type="color">
                    </div>
                    <div class="item">
                        <label>progressBarBg</label>
                        <input v-model="progressBarBg" type="color">
                    </div>
                    <div class="item">
                        <label>completedBg</label>
                        <input v-model="completedBg" type="color">
                    </div>
                    <div class="item">
                        <label>handlerBg</label>
                        <input v-model="handlerBg" type="color">
                    </div>
                </div>
            </section>
        </div>
        <footer>
            <div class="container">
                <div> &copy; 2021 Anton Zolotov </div>
