    text-rendering: optimizeLegibility;
}
.container {
    width: 60rem;
    max-width: 96%;
    margin: 0 auto;
    &.is-content {
        width: 35rem;
    }
}

a {
    line-height: inherit;
    transition: color .2s ease, border-bottom-color .2s ease;
    border: none;
    font-family: inherit;
    color: var(--color-theme);
    text-decoration: none;
    cursor: pointer;
}

h1,
h2,
h4 {
    margin: 0;
    margin-bottom: 0.5rem;
    font-weight: 800;
    font-style: normal;
    color: inherit;
