    column-gap: 1rem;
    row-gap: 0.5rem;
    .item {
        max-width: 100%;
         overflow: hidden;
        label {
            display: block;
            margin-bottom: 0.25rem;
            font-size: 0.875rem;
        }
        input {
            box-sizing: border-box;
        }
        input[type=text],
        input[type=color],
        input[type=number] {
            background-color: var(--color-bg);
            border: 1px solid var(--color-lightgray);
            border-radius: 0.25rem;
            color: var(--color-gray);
            cursor: text;
            display: block;
            font-size: 0.75rem;
            height: 2rem;
            line-height: 0.75rem;
            max-width: 100%;
            min-width: 100%;
            padding: 0.125rem 0.5rem;
            position: relative;
            transition: all 0.2s;
            width: 100%;
            &:hover {
                border-color: var(--color-mediumgray);
            }
            &:focus,
            &.focus {
                border-color: var(--color-theme);
                outline: none;
            }
        }
        input[type=color] {
            padding: 0;
            border: none;
            cursor: pointer;
        }
    }
}

nav,
footer {
    background-color: var(--color-bg);
    width: 100%;
}

nav {
    border-bottom: 1px solid var(--color-lightgray);
    height: 50px;
    left: 0;
    position: fixed;
    top: 0;
    z-index: 5;

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        > div {
            display: flex;
            align-items: center;
        }
    }

    h4 {
        line-height: 50px;
        font-size: 1rem;
        margin: 0;
    }
    img {
        width: 24px;
        height: auto;
        margin-right: 0.5rem;
    }

    .nav-item {
        color: var(--color-gray);
        font-size: 12px;
        font-weight: bold;
        margin-left: 1rem;
        transition: all .3s ease;
        &:hover{
            color: var(--color-theme);
        }
    }

    .nav-icon {
        fill: var(--color-gray);
        height: auto;
        margin-top: 4px;
        transition: all .3s ease;
        width: 20px;
        &:hover{
            fill: var(--color-theme);
        }
    }
}

footer {
    border-top: 1px solid var(--color-lightgray);
